.main__games-item:hover img {
    transform: scale(1.05);
}
.main__games-item--grid {
    transition: 0.3s;
    flex-basis: 35%;
    margin: 25px auto;
}
.main__games-item--grid:hover {
    transform: scale(1);
}
.main__games-item--grid .main__games-img {
    height: 16rem;
}
@media screen and (max-width:1024px) {
    .main__games-item--grid {
        flex-basis: 100%;
    }
    .main__games-item--grid .main__games-img {
        height: 18rem;
    }
}