@import url('https://fonts.googleapis.com/css2?family=Raleway&family=Rubik+Puddles&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;
* {
    font-family: 'Raleway', sans-serif;
}

.header__logo-link,
.header__logo-link span {
    font-family: 'Rubik Puddles', cursive;
    color: #8B0000;
}

@media (max-width: 768px) {
    .main__sidebar * {
        font-size: 14px;
    }

    .main__sidebar ul.space-y-1 {
        padding-top: 0;
    }
}

@media screen and (max-width: 430px) {
    .header__logo-link span:nth-child(2),
    .header__logo-link span:nth-child(3) {
        display: none;
    }

    .sidebar__btn {
        display: block;
    }

    .main__sidebar {
        transition: .4s;
        position: fixed;
        z-index: 10000;
        width: 50vw;
        background: #131419;
        top: 0;
        transform: translateX(-100%);
    }

    .main__sidebar .sidebar__btn {
        display: block;
        left: 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }

    .main__sidebar.translate {
        transform: translateX(0);
    }

    .sidebar__btn.translate {
        transform: translateX(50vw);
    }

    .main__sidebar-header * {
        color: #fff;
    }

    .main__sidebar span.ml-2,
    .main__sidebar li.px-5 div div {
        color: #fff;
    }

    .filters__years {
        display: none;
    }


    .main__section-content {
        width: 100%;
        padding: 0 10px;
    }

    .main__games-item {
        flex-basis: 100%;
    }

    .game__detail-similar-item {
        flex-basis: 48%;
    }
    .game__detail-header {
        flex-direction: column;
    }
    .system {
        flex-direction: column;
    }
}

@media screen and (max-width: 768px) {
    .header__logo-link span:nth-child(1),
    .header__logo-link span:nth-child(4) {
        display: inline;
    }

    .header__logo-link span:nth-child(2),
    .header__logo-link span:nth-child(3) {
        display: none;
    }

    .sidebar__btn {
        display: none;
    }
}

@media screen and (max-width: 720px) {
    .header__logo-link span:nth-child(2),
    .header__logo-link span:nth-child(3) {
        display: none;
    }

    .header__inner-search {
        width: 50%;
    }

    .header__inner-search input {
        font-size: 12px;
    }

    .sidebar__btn {
        display: block;
    }

    .main__sidebar {
        transition: .4s;
        position: fixed;
        z-index: 10000;
        width: 50vw;
        background: #131419;
        top: 0;
        left: 0;
        height: 100vh;
        transform: translateX(-100%);
        overflow-y: scroll;
    }

    .main__sidebar.translate {
        transform: translateX(0);
    }

    .sidebar__btn.translate {
        transform: translateX(50vw);
    }

    .main__sidebar-header * {
        color: #fff;
    }

    .main__sidebar span.ml-2,
    .main__sidebar li.px-5 div div {
        color: #fff;
    }

    .filters__years {
        display: none;
    }


    .main__section-content {
        width: 100%;
        padding: 0 10px;
    }

    .main__games-item {
        flex-basis: 100%;
    }

    .filters__platfoms div.origin-top-right {
        column-count: 2;
    }
}

@media (min-width: 768px) and (max-width: 1920px) {
    .game__detail-similar-item {
        flex-basis: 24%;
    }
}